/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-ridges-home-hero {
	position: relative;
	border-bottom: 1px solid hsl(46deg 16% 68%);
	padding-bottom: 4rem;
	margin-top: -3em;
}

.home-hero__img-wrapper {
	position: relative;
	padding-top: 92%;
	overflow: hidden;
}

.home-hero__img-column {
	position: relative;
	margin-bottom: 2rem;
	overflow: hidden;
}

.home-hero__img-wrapper img {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0; 
	margin: auto;
	min-width: 100%;
	min-height: 100%;
	object-fit: cover;
}

.home-hero__path-2--mobile {
	position: absolute;
	width: 100%;
	top: 45%;
	right: -27%;
	z-index: 1;
}

.home-hero__path-secondary-container {
	position: absolute;
	display: none;

	bottom: 0;
	right: 0;
	width: 37%;
	height: 900px;
	overflow: hidden;
}

.home-hero__path-2--desktop {
	position: absolute;
	width: 150%;
	z-index: 1;
	max-width: none;
	stroke-width: 37px;
	bottom: -4rem;
}

.home-hero__path-1 {
	display: none;
	position: absolute;
  width: 5139px;
  max-width: none !important;
	top: -52rem;
	right: calc(50% - 41rem);

  z-index: -1;
}

.home-hero__path-1 path {
	stroke-width: 37px;
}

.home-hero__path-1-loop {
	display: none;
	position: absolute;
	width: 427px;

  top: -6.8rem;
  right: calc(50% - 14.15rem);

}

.home-hero__path-1-loop path {
	stroke-width: 37px !important;
}

.home-hero__img-attribution {
	position: absolute;
	bottom: 0;
  font-size: 0.875rem;
  font-style: italic;
  margin-left: 20px;
  margin-right: 20px;
}

@media only screen and (min-width: 30rem) {

	.home-hero__img-attribution {
	  margin-left: 45px;
	  margin-right: 45px;
	}

}

@media only screen and (min-width: 48rem) {

	.wp-block-ridges-home-hero {
		position: relative;
	}

	.home-hero__path-2--mobile, .editor-styles-wrapper .home-hero__path-2--mobile {
		display: none;
	}

	.home-hero__img-column {
    width: 50%;
    width: calc(50% + 4rem);
    position: absolute;
    height: 100%;
    right: 0;
  }

	.home-hero__img-wrapper {
		position: absolute;
		top: 0;
		bottom: 4rem;
		right: 0;
		width: 100%;
		padding-top: 0;
		border-top-left-radius: 15px;
		border-bottom-left-radius: 15px;
	}

	.wp-block-ridges-home-hero .container {
		overflow: auto;
	}

	.home-hero__content-container {
		width: 50%;
		width: calc(50% - 8rem);
		max-width: 25rem;
		margin-top: 4.5rem;
		margin-bottom: 4.5rem;
	}

	.home-hero__path-secondary-container {
		display: block;
	}

	.home-hero__img-attribution {
		margin-left: 0;
		bottom: 1rem;
	}

}

@media only screen and (min-width: 82rem) {

	.home-hero__path-1, .home-hero__path-1-loop {
		display: block;
	}

}
